.OrderAside {
  margin: -16px 30px -1px 20px;
  padding: 20px 0 20px 0px;
  border-right: 1px solid #EAEAEA;
  border-top: 1px solid #EAEAEA;
  border-bottom: 1px solid white; }

.DishesContainer {
  padding-left: 20px; }

.OrderContainer {
  border-top: 3px solid #EAEAEA;
  margin-top: 50px; }
  .OrderContainer .OrderTitle {
    display: flex;
    flex-flow: wrap;
    justify-content: space-between;
    padding-right: 20px; }
    .OrderContainer .OrderTitle span {
      color: rgba(234, 234, 234, 0.3); }
  .OrderContainer .OrderSummarize {
    display: flex;
    flex-flow: wrap;
    justify-content: space-between;
    padding-right: 20px; }
  .OrderContainer .Button {
    background-color: #EAEAEA;
    color: black;
    text-transform: uppercase;
    padding: 10px;
    border: none;
    font-size: 16px;
    font-weight: bold; }

@media (max-width: 600px) {
  .OrderAside {
    margin: 0px 30px -1px 20px; } }



