.Layout {
  background-image: url("./../../assets/images/background.webp");
  height: 100%;
  width: 100%;
  position: fixed;
  background-position: center;
  background-size: cover;
  overflow-y: auto; }

.Container {
  margin: 0 20px 0 0; }

@media (max-width: 600px) {
  .Container {
    margin: 0; } }



