.ItemContainer {
  border-bottom: 1px solid #EAEAEA;
  cursor: pointer;
  padding: 20px; }
  .ItemContainer .Item {
    display: flex;
    width: 100%;
    justify-content: space-between; }
  .ItemContainer p {
    text-align: left;
    padding-right: 40px; }
  .ItemContainer .Img {
    width: 100%;
    height: 100%;
    padding-right: 16px; }
  .ItemContainer button {
    margin: 0 10px;
    color: white;
    border: 1px solid inherit;
    border-radius: 10px; }
    .ItemContainer button:hover {
      color: #1E1E1E; }

.ItemContainer:hover {
  background-color: rgba(234, 234, 234, 0.05); }

.Add {
  background-color: green; }



