.RestaurantContainer {
  background-color: #1E1E1E;
  width: 100%;
  padding: 2rem;
  margin: 0 20px; }
  .RestaurantContainer .TitleImg {
    margin-bottom: 16px; }
  .RestaurantContainer img {
    width: 100%;
    height: 100%; }

.RestaurantItem:hover > div {
  transform: scale(0.98);
  transition: 1s; }

@media (max-width: 600px) {
  .RestaurantContainer {
    margin: 0; } }



