.Dropdown {
  margin-bottom: 16px;
  cursor: pointer; }
  .Dropdown p {
    margin: 0; }
  .Dropdown li {
    text-transform: uppercase;
    margin: 4px 0; }
    .Dropdown li:hover {
      color: rgba(234, 234, 234, 0.3); }
    .Dropdown li a {
      color: inherit; }
    .Dropdown li a:hover {
      color: rgba(234, 234, 234, 0.3); }
    .Dropdown li .Active {
      color: rgba(234, 234, 234, 0.3); }
  .Dropdown .DishesMenu {
    display: flex;
    justify-content: space-between;
    flex-flow: wrap;
    padding-right: 8px;
    align-items: center; }
    .Dropdown .DishesMenu:hover > p {
      color: rgba(234, 234, 234, 0.3); }
    .Dropdown .DishesMenu:hover > i {
      color: rgba(234, 234, 234, 0.3); }
  .Dropdown .HiddenMenu {
    overflow: hidden; }
  .Dropdown ul {
    border-top: 1px solid white; }

.Dropdown i {
  transition: transform 0.5s; }

.HideMenu ul {
  max-height: 0;
  transition: max-height 0.3s ease-out; }

.ShowMenu ul {
  max-height: 300px;
  transition: max-height 1s ease-in; }

.ShowMenu i {
  transform: rotate(180deg); }



