.SideDrawer {
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 200;
  background-color: #1E1E1E;
  transition: width 0.3s ease-out;
  padding: 50px;
  border: 1px solid #656565; }

.Open {
  width: 294%;
  padding: 50px; }

.Close {
  padding: 0;
  width: 0;
  overflow: hidden;
  border: none; }

.OrderContainer {
  border: 1px solid #656565;
  height: calc(100% - 108px); }
  .OrderContainer .OrderTitle, .OrderContainer .OrderSummarize {
    margin: 20px;
    padding-bottom: 20px;
    justify-content: space-between; }
  .OrderContainer .OrderTitle {
    border-bottom: 1px solid #656565; }
  .OrderContainer div {
    display: flex; }
  .OrderContainer h5 {
    margin: 0;
    font-size: 30px; }
  .OrderContainer span, .OrderContainer p {
    font-size: 20px; }
  .OrderContainer p {
    margin-left: 10px; }
  .OrderContainer .OrderSummarizePrice {
    font-size: 30px; }

.OrderList {
  display: flex;
  flex-flow: column;
  border-bottom: 1px solid #EAEAEA; }
  .OrderList li {
    margin: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between; }

.Button {
  background-color: #1E1E1E;
  border: 1px solid #EAEAEA;
  color: #EAEAEA;
  text-transform: uppercase;
  padding: 20px 40px;
  font-size: 16px;
  font-weight: bold;
  margin: 40px;
  height: 68px; }

.Arrow:before {
  content: "<";
  margin-right: 10px; }

@media (max-width: 600px) {
  .Open {
    width: 100%;
    padding: 50px; }
  .Close {
    padding: 0;
    width: 0;
    overflow: hidden;
    border: none; }
  .Button {
    text-transform: uppercase;
    padding: 20px 20px;
    font-size: 16px;
    font-weight: bold;
    margin: 10px;
    height: 68px; } }



