.NavigationBtn {
  background: transparent;
  border: 1px solid transparent;
  font-size: 14px;
  height: 25px;
  padding: 0;
  width: 25px;
  position: relative;
  cursor: pointer; }
  .NavigationBtn:focus {
    outline: none; }
  .NavigationBtn span {
    background: #EAEAEA;
    height: 2px;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: background 0.4s;
    width: 22px;
    z-index: 500px; }
    .NavigationBtn span::before, .NavigationBtn span::after {
      content: "";
      background: #EAEAEA;
      height: 2px;
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
      transition: top 0.5s, left 0.5s, transform 0.5s;
      width: 22px; }
    .NavigationBtn span::before {
      top: 8px; }
    .NavigationBtn span::after {
      top: -8px; }

.Active span {
  background: rgba(0, 0, 0, 0); }
  .Active span::before {
    left: 0;
    top: 0;
    transform: rotate(45deg); }
  .Active span::after {
    left: 0;
    top: 0;
    transform: rotate(135deg); }



