.Navigation {
  width: 100%;
  display: flex;
  align-items: center; }
  .Navigation .NavigationMenu {
    display: flex; }
    .Navigation .NavigationMenu li {
      align-items: center;
      display: flex;
      width: 100%; }
    .Navigation .NavigationMenu a, .Navigation .NavigationMenu i {
      color: #efefef;
      margin: 5px auto;
      text-transform: uppercase;
      text-shadow: 1px 1px 5px #000;
      white-space: nowrap;
      cursor: pointer; }
      .Navigation .NavigationMenu a:hover, .Navigation .NavigationMenu i:hover {
        color: #FFFFFF;
        text-decoration: none; }
    .Navigation .NavigationMenu a {
      font-size: 14px; }
    .Navigation .NavigationMenu i {
      width: 100%;
      text-align: end; }
    .Navigation .NavigationMenu .Active {
      color: #B2B1B1; }
      .Navigation .NavigationMenu .Active:hover {
        color: #B2B1B1; }

@media (max-width: 600px) {
  .Navigation {
    display: none; } }



