.FullProduct {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center; }
  .FullProduct h3 {
    color: #d8b093;
    margin: 25px 0; }
  .FullProduct i,
  .FullProduct a {
    color: grey;
    cursor: auto; }
  .FullProduct .ActiveLink:hover {
    color: white;
    transform: scale(1.1);
    transition: 0.5s;
    cursor: pointer; }
  .FullProduct .ProductContent {
    margin: 30px; }
  .FullProduct .ProductImg {
    width: 100%;
    height: 100vh;
    display: flex;
    margin: 0; }
    .FullProduct .ProductImg img {
      width: 100%;
      object-fit: contain; }

.FullButton {
  position: absolute;
  top: 50px;
  left: 50px; }

.CloseButton {
  position: absolute;
  right: 50px;
  top: 50px; }
  .CloseButton:hover {
    color: white;
    transform: scale(1.1);
    transition: 0.5s;
    cursor: pointer; }



