.SliderItem {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  width: 100%;
  position: absolute;
  opacity: 0;
  left: 0%;
  top: 0%; }
  .SliderItem img {
    width: 100%;
    height: 100%;
    max-height: 376px; }

.Background {
  position: relative;
  transition: 1s ease-out;
  opacity: 1; }

.BackgroundHide {
  transition: 1s ease;
  opacity: 0; }



