.Contact {
  margin: 0 20px;
  background-color: #1E1E1E;
  width: 100%;
  padding: 2rem; }
  .Contact form {
    display: flex;
    flex-flow: column; }
    .Contact form .ButtonContainer {
      display: flex;
      flex-flow: row;
      align-items: center;
      align-content: space-between;
      justify-content: flex-end;
      margin-top: 10px; }
    .Contact form button {
      background-color: #333333;
      border: 0;
      padding: 5px 15px;
      color: #dcd4d4; }
    .Contact form button:disabled {
      background-color: #dcd4d4;
      color: rgba(51, 51, 51, 0.2); }

iframe {
  width: 100%;
  height: 300px; }

@media (max-width: 600px) {
  .Contact {
    margin: 0; } }



