.ModalProduct img {
  width: 100%; }

.ModalProduct .Container {
  display: flex;
  flex-flow: column;
  padding: 16px; }
  .ModalProduct .Container h5 {
    text-transform: uppercase;
    font-size: 24px; }
  .ModalProduct .Container p {
    text-align: left; }

.ModalProduct .Description {
  color: #B1B1B1; }

.ModalProduct .Button {
  background-color: #EAEAEA;
  color: black;
  text-transform: uppercase;
  padding: 10px;
  border: none;
  font-size: 16px;
  font-weight: bold; }



