.NavMobile {
  display: none;
  position: relative;
  width: 100%;
  margin-bottom: 5px; }
  .NavMobile .NavContainer {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 25px 25px 0 0;
    z-index: 500; }
  .NavMobile .NavMenu {
    background-color: #1E1E1E;
    position: absolute;
    left: 10%;
    top: calc( 100% - 25px);
    transform-origin: 100% 0%;
    width: 80%;
    border: 1px solid #EAEAEA; }
    .NavMobile .NavMenu ul {
      margin: 0; }
    .NavMobile .NavMenu li:not(:first-child) {
      border-top: 1px solid #efefef; }
    .NavMobile .NavMenu a, .NavMobile .NavMenu .SignIn {
      color: #efefef;
      display: flex;
      padding: 10px 25px;
      margin: 5px auto;
      text-align: center;
      text-transform: uppercase;
      text-shadow: 1px 1px 1px #000; }
      .NavMobile .NavMenu a:hover, .NavMobile .NavMenu .SignIn:hover {
        color: #FFFFFF;
        text-decoration: none; }
    .NavMobile .NavMenu .SignIn {
      cursor: pointer; }
    .NavMobile .NavMenu .Active {
      color: #B2B1B1; }
      .NavMobile .NavMenu .Active:hover {
        color: #B2B1B1;
        text-decoration: none; }
  .NavMobile .Active {
    color: #B2B1B1; }

@media (max-width: 600px) {
  .NavMobile {
    display: flex; } }

.Show {
  transition: all 1s;
  opacity: 1;
  transform: scaleY(1); }

.Hide {
  transition: all 1s;
  opacity: 0;
  transform: scaleY(0); }



