.Input {
  width: 100%;
  box-sizing: border-box; }
  .Input textarea {
    min-height: 200px; }

.InputElement:focus + p {
  display: none; }

.ErrorMsg {
  display: block;
  height: 16px;
  color: red;
  margin-top: 10px;
  text-align: left; }

.Label {
  display: block; }

.InputElement {
  border: 1px solid #333333;
  background-color: rgba(101, 101, 101, 0.5);
  color: #EAEAEA;
  font: inherit;
  padding: 6px 10px;
  display: block;
  width: 100%; }

.InputElement:focus {
  outline: none;
  background-color: rgba(101, 101, 101, 0.9);
  border: none; }

.Invalid {
  border: 1px solid red; }



